<template>
    <div class="city_container">
        <header>
            <i class="el-icon-arrow-left" @click="goHome"></i>
            <section>
                <span>{{city_name}}</span>
            </section>
            <router-link to="/home">切换城市</router-link>
        </header>
        <form class="city_form">
            <div>
                <input type="search" placeholder="输入学校、商务楼、地址" class="city_lookfor" v-model="sc_text">
            </div>
            <div>
                <input type="submit" value="提交" class="city_submit" @click="getCityMsg">
            </div>
        </form>
        <div class="search_tip" v-if="isShow">搜索历史</div>
        <ul class="search_ul">
            <li v-for="(list, index) in searchCity" :key="index" @click="setlocalstorage(list)">
                <h4>{{list.name}}</h4>
                <p>{{list.address}}</p>
            </li>
        </ul>
        <footer class="clearmsg" @click="clearmsg" v-if="searchCity.length ? true : false">清空所有</footer>
    </div>

</template>

<script>
    export default {
        name: "City",
        data(){
            return {
                searchCity:[],
                sc_text:"",
                city_id:"",
                city_name:"",
                isShow:true
            }
        },
        created(){
            this.city_id = this.$route.params.cityId;
            this.axios.get("/v1/cities/"+this.$route.params.cityId).then(res=>{
                this.city_name = res.name;
            }).catch(err=>{});
            if(localStorage.placeHistory != undefined){
                this.searchCity = JSON.parse(localStorage.getItem("placeHistory"));
            }
            // console.log(localStorage.placeHistory);
        },





        methods:{
            getCityMsg(){
                this.isShow = false;
                this.axios.get("/v1/pois",{
                    params:{city_id:this.$route.params.cityId, keyword:this.sc_text}
                }).then(res=>{
                    this.searchCity = res;
                }).catch(err=>{});
            },
            setlocalstorage(addlist){
                console.log(addlist);
                localStorage.setItem("placeNameHistory",addlist.name);
                if(!localStorage.placeHistory){
                    //当localStorage为空时创建数组，并插入到localStorage中
                    let arr = [addlist];
                    localStorage.setItem("placeHistory",JSON.stringify(arr));
                }else {
                    //当localStorage不为空时，为避免重复插入进行判断过滤
                    let newarr = JSON.parse(localStorage.getItem("placeHistory"));

                    let filterArr = newarr.filter(v=>{
                        return v.name == addlist.name;
                    });
                    // console.log(filterArr);
                    if(!filterArr.length){
                        newarr.push(addlist);
                        localStorage.setItem("placeHistory",JSON.stringify(newarr));
                    }
                    // console.log(JSON.parse(localStorage.getItem("placeHistory")));
                }
                this.$router.push("/msite");
            },
            clearmsg(){
                localStorage.clear();
                this.searchCity.splice(0, this.searchCity.length);
            },
            goHome(){
                this.$router.push("/home");
            }
        }
    }
</script>

<style scoped>
    .city_container{
        overflow: hidden;
    }
    header{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>i{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        line-height: 2.2rem;
        margin-left: .4rem;
    }
    section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    section>span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    header>a{
        right: .4rem;
        font-size: .6rem;
        color: #fff;
        position: absolute;
        top: 50%;
        text-decoration: none;
        transform: translateY(-50%);
    }
    form{
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-top: .4rem;
        display: block;
        margin-top: 2.4em;
    }
    form>div{
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    form>div>input{
        border-radius: .1rem;
        margin-bottom: .4rem;
        width: 100%;
        height: 1.4rem;
    }
    .city_lookfor{
        border: 1px solid #e4e4e4;
        padding: 0 .3rem;
        font-size: .65rem;
        color: #333;
    }
    .city_submit{
        background-color: #3190e8;
        font-size: .65rem;
        color: #fff;
        border:none;
        /*background:none;*/
        cursor:pointer;
    }
    .search_tip{
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-left: .5rem;
        font: .475rem/.8rem Microsoft YaHei;
    }
    .search_ul{
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
    }
    .search_ul>li{
        margin: 0 auto;
        padding-top: .65rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .search_ul>li>h4{
        margin: 0 auto .35rem;
        width: 90%;
        font-size: .65rem;
        color: #333;
    }
    .search_ul>li>p{
        width: 90%;
        margin: 0 auto .55rem;
        font-size: .45rem;
        color: #999;
    }
    .clearmsg{
        font-size: .7rem;
        color: #666;
        text-align: center;
        line-height: 2rem;
        background-color: #fff;
    }
</style>